export default {
  data() {
    return {
      isRouterAlive: true
    };
  },

  provide() {
    // 注册一个方法
    return {
      reload: this.reload
    };
  },

  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function () {
        this.isRouterAlive = true;
      });
    }

  }
};