export default {
  data() {
    return {
      message: "",
      isShow: false
    };
  },

  methods: {
    show(message, duration) {
      this.isShow = true;
      this.message = message;
      setTimeout(() => {
        this.isShow = false;
        this.message = '';
      }, duration);
    },

    open() {
      this.$message('这是一条消息提示');
    }

  }
};