import { mock } from '@/mock/index';
export default {
  data() {
    return {
      input: '',
      time: 300,
      restaurants: mock
    };
  },

  methods: {
    change(v) {
      console.log('v:' + v);
      if (v.charCodeAt() == 32) return;
      this.$emit('_search', v);
    },

    fn() {
      this.$emit('_search', -1);
    },

    querySearch(queryString, cb) {
      var restaurants = this.restaurants; // var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;

      console.log(queryString); // 调用 callback 返回建议列表的数据

      cb(restaurants);
    },

    createFilter(queryString) {
      return restaurants => {
        return restaurants.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0;
      };
    }

  }
};